* {
  /*Global Selector*/
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #57534e;
  color: #fafaf9;
  font-family: "sono", sans-serif;
  padding: 48px 64px 0;
}

.disputed {
  color: #c53030;
  font-weight: 600;
  margin-right: 10px;
}

/* Main Layout*/
#root {
  max-width: 1500px;
  margin: 0 auto;
  height: max-content;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 28px;
  height: calc(100vh - 48px - 68px - 5px);
  position: relative;
  ::-webkit-scrollbar {
    display: none;
  }
  body {
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* IE and Edge */
  }
}

@media (max-width: 900px) {
  .main {
    grid-template-columns: 1fr;
    height: auto;
  }
}

section {
  overflow: scroll;
  padding-bottom: 40px;
}

