/* Elements */
.logo {
  display: flex;
  align-items: center;
  gap: 16px;
}

.logo img {
  width: 64px;
  height: 64px;
}

h1 {
  font-size: 42px;
  text-transform: uppercase;
  font-family: "coiny", sans-serif;
  line-height: 1px;
  margin-top: 6px;
}

.fact {
  font-size: 20px;
  line-height: 1.4;
  font-family: "sono", sans-serif;
  background-color: #44403c;
  margin-bottom: 16px;
  margin-left: 50px;
  padding: 16px 24px;
  letter-spacing: -1px;
  display: flex;
  align-items: center;
  gap: 24px;
  border-radius: 16px;
}

.tag {
  text-transform: uppercase;
  font-size: 14px;
  font-family: "coiny", sans-serif;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 3px;
  border-radius: 100px;
}

.vote-buttons {
  margin-left: auto;
  flex-shrink: 0;
  display: flex;
  gap: 4px;
}

.vote-buttons button {
  background-color: #57534e;
  border: none;
  font-size: 18px;
  padding: 6px 12px;
  border-radius: 100px;
  color: inherit;
  font-family: inherit;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
}

.vote-buttons button:hover {
  background-color: #292524;
}

.vote-buttons button:disabled {
  background-color: #504b49;
}

ul {
  list-style: none;
}

.source:link,
.source:visited {
  color: #a8a29e;
  margin-left: 12px;
  text-decoration: none;
  transition: 0.3s;
}

.source:hover,
.source:active {
  color: #3b82f6;
}

.btn {
  font-family: "coiny", sans-serif;
  line-height: 1;
  text-transform: uppercase;
  font-size: 17px;
  padding: 16px 0 13px;
  background-image: linear-gradient(135deg, #cbe4de, #0e8388, #2e4f4f, #2c3333);
  border: none;
  color: inherit;
  transition: 0.3s;
  border-radius: 100px;
  cursor: pointer;
}

.btn:hover {
  transform: scale(110%) rotate(-2deg);
}

.btn-large {
  font-size: 20px;
  padding: 20px 32px 17px;
}

.btn-all-category {
  margin-bottom: 16px;
  width: 100%;
}

.btn-category {
  width: 100%;
  background-image: none;
}

.category {
  margin-bottom: 16px;
}

.fact-count {
  font-size: 18px;
  color: #89aa97;
  text-align: center;
  line-height: 1.4;
  font-family: "sono", sans-serif;
